  private selectChange(value: string) {
    if(value === 'intrusion') {
      this.intrusionStyle = false;
      this.$nextTick(() => {
        this.svgOptions = { type: 'polygon', name: value, color: 'red' };
        this.uploadData = { confidence: 0.8, nms: 0.4, type: 'intrusion',region: '',};
